* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background: linear-gradient(#000 50%, #ffb92a 50%);
  height: 100vh;
}

.container {
  background-color: #201f28;
  border-radius: .6em;
  width: 90vw;
  max-width: 75em;
  max-height: 80vh;
  padding: 3em 1.8em;
  font-size: 16px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  box-shadow: 1.2em 2em 3em #0003;
}

.container::-webkit-scrollbar {
  width: 6px;
}

.container::-webkit-scrollbar-thumb {
  background-color: #fff;
  border-radius: 3px;
}

.container::-webkit-scrollbar-track {
  background-color: #201f28;
}

.container::-webkit-scrollbar-thumb:hover {
  background-color: #fff;
}

.search-container {
  grid-template-columns: 9fr 3fr;
  gap: 1.2em;
  display: grid;
}

.search-container input {
  color: #fff;
  background-color: #0000;
  border: 1px solid #a0a0a0;
  border-radius: .3em;
  outline: none;
  padding: .7em;
  font-size: .9em;
}

.search-container input:focus {
  border-color: #fff;
}

.search-container button {
  cursor: pointer;
  background-color: #ffb92a;
  border: none;
  border-radius: .3em;
  outline: none;
  font-size: .9em;
  transition: background-color .3s, letter-spacing .3s;
}

.search-container button:hover {
  letter-spacing: 5px;
  background-color: #fff;
}

#result {
  color: #fff;
}

.info {
  grid-template-columns: 4fr 8fr;
  align-items: center;
  margin-top: 1.2em;
  display: grid;
  position: relative;
}

.poster {
  border: 2px solid #0000;
  width: 100%;
  transition: border-color .3s;
}

.poster:hover {
  border-color: #ffb92a;
}

h2 {
  text-align: center;
  letter-spacing: .06em;
  font-size: 1.5em;
  font-weight: 600;
  animation: 2s infinite alternate shadowAnimation;
}

@property --angle {
  syntax: "<angle>";
  inherits: true;
  initial-value: 90deg;
}

@property --gradX {
  syntax: "<percentage>";
  inherits: true;
  initial-value: 50%;
}

@property --gradY {
  syntax: "<percentage>";
  inherits: true;
  initial-value: 0%;
}

.movie-title {
  border: .35rem solid;
  border-image: conic-gradient(from var(--angle), var(--c2), var(--c1) .1turn, var(--c1) .15turn, var(--c2) .25turn) 30;
  animation: borderRotate var(--d) linear infinite forwards;
  margin: max(1rem, 3vw);
  padding: 3vw;
  font-size: 3vw;
}

.movie-title:nth-child(2) {
  border-image: radial-gradient(ellipse at var(--gradX) var(--gradY), var(--c1), var(--c1) 10%, var(--c2) 40%) 30;
  animation: borderRadial var(--d) linear infinite forwards;
}

@keyframes borderRotate {
  100% {
    --angle: 420deg;
  }
}

@keyframes borderRadial {
  20% {
    --gradX: 100%;
    --gradY: 50%;
  }

  40% {
    --gradX: 100%;
    --gradY: 100%;
  }

  60% {
    --gradX: 50%;
    --gradY: 100%;
  }

  80% {
    --gradX: 0%;
    --gradY: 50%;
  }

  100% {
    --gradX: 50%;
    --gradY: 0%;
  }
}

.rating {
  justify-content: center;
  align-items: center;
  gap: .6em;
  margin: .6em 0 .9em;
  display: flex;
}

.rating img {
  width: 2em;
}

.rating h4 {
  font-size: 1.1em;
  font-weight: 500;
  display: inline-block;
}

.details {
  color: #a0a0a0;
  justify-content: center;
  gap: 1em;
  margin: .6em 0;
  font-size: .95em;
  font-weight: 300;
  display: flex;
}

.genre {
  justify-content: space-around;
  display: flex;
}

.genre div {
  border: 1px solid #a0a0a0;
  border-radius: .4em;
  padding: .4em 1.6em;
  font-size: .75em;
  font-weight: 300;
}

h3 {
  color: #a0a0a0;
  margin-top: 1.2em;
  font-weight: 500;
}

p {
  text-align: justify;
  color: #a0a0a0;
  font-size: .9em;
  font-weight: 300;
  line-height: 1.8em;
}

.msg {
  text-align: center;
}

.review {
  border: 1px solid #ccc;
  margin-bottom: 20px;
  padding: 10px;
  display: flex;
}

.avatar {
  margin-right: 10px;
}

.avatar img {
  border-radius: 50%;
  width: 45px;
  height: 45px;
  transition: transform .5s;
}

.avatar img:hover {
  transform: rotate(360deg)scale(1.2);
}

.review-info {
  flex: 1;
}

.read-more-btn {
  color: #fff;
  text-align: center;
  background-color: #ffb92a;
  border-radius: 5px;
  width: 25%;
  margin-top: 10px;
  padding: 8px 12px;
  text-decoration: none;
  transition: background-color .3s, width .3s;
  display: inline-block;
}

.read-more-btn:hover {
  color: #000;
  background-color: #fff;
  width: 50%;
}

@media screen and (width <= 600px) {
  .container {
    font-size: 14px;
  }

  .info {
    grid-template-columns: 1fr;
  }

  .poster {
    width: auto;
    max-height: 10.8em;
    margin: auto;
  }
}

@keyframes shadowAnimation {
  0% {
    text-shadow: 2px 2px 5px #ffffff80;
  }

  50% {
    text-shadow: 4px 4px 8px #fffc;
  }

  100% {
    text-shadow: 2px 2px 5px #ffffff80;
  }
}

